export class ClanUO {
  ime: String;
  prezime: String;
  uloga: String;
  slika: String;
  constructor(ime: String, prezime: String, uloga: String, slika: String) {
    this.ime=ime;
    this.prezime=prezime;
    this.uloga=uloga;
    this.slika=slika;
  }
}



